
html, body {
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
#app .header ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
#app .header ul li {
      border-bottom: 1px solid #999;
      padding: 1rem;
      width: 50%;
}
#app .header ul .active {
      color: #fe5e5e;
      border-bottom: 2px solid #fe5e5e;
}
* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}



