
.border-none:after {
  display: none;
}
.clearFix {
  display: inline-block;
}
.clearFix:after {
    display: block;
    content: ".";
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
}
.forms {
  text-align: left;
  padding: 1rem;
}
.forms .usr-con {
    padding: 1rem 0;
    position: relative;
    padding-left: 0.5rem;
}
.forms .usr-con:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      border-bottom: 1px solid #999;
      width: 100%;
      height: 100%;
      border-radius: 0;
      content: '';
      z-index: -1;
}
.forms .dinner-con {
    padding: 1rem 0;
    position: relative;
    padding-left: 0.5rem;
}
.forms .dinner-con:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      border-bottom: 1px solid #999;
      width: 100%;
      height: 100%;
      border-radius: 0;
      content: '';
      z-index: -1;
}
.forms input {
    border: none;
    outline: none;
}
.forms .dinner-type {
    margin: 1rem auto;
}
.forms .dinner-type ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.forms .dinner-type ul li {
        width: 30%;
        text-align: center;
        position: relative;
        padding: 0.6rem 0;
        border-radius: 5px;
        margin: 1rem 0;
}
.forms .dinner-type ul li:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          border: 1px solid #fe5e5e;
          width: 100%;
          height: 100%;
          border-radius: 3px;
          content: '';
          z-index: -1;
}
.forms .dinner-type ul li input[type="radio"] {
          display: none;
}
.forms .dinner-type ul li input[type="radio"]:checked + label {
          color: #fe5e5e;
          font-size: 1.2rem;
          font-weight: bold;
}
.forms .submit-btn {
    width: 80%;
    background-color: #333;
    padding: 1rem;
    font-size: 1.6rem;
    margin: 1rem auto 0;
    text-align: center;
    color: #fff;
}
.forms .active {
    color: #fe5e5e;
}



