
.border-none:after {
  display: none;
}
.clearFix {
  display: inline-block;
}
.clearFix:after {
    display: block;
    content: ".";
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
}
.recordList {
  text-align: left;
}
.recordList li {
    padding: 1rem;
    overflow: hidden;
    position: relative;
}
.recordList li:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      border-bottom: 1px solid #999;
      width: 100%;
      height: 100%;
      border-radius: 0;
      content: '';
      z-index: -1;
}
.recordList li .usr-con {
      float: left;
}
.recordList li .add_time {
      float: right;
}
.loads {
  margin-top: 2rem !important;
}



